.navigation {
  position: relative;
  z-index: 1;


  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: baseline;
    display: none;
  }

  &__list {
    @include ListReset();

    flex-wrap: wrap;
    flex-direction: column;

    width: 100%;
    margin-left: auto;
    margin-right: 0;
  }

  &__link {
    position: relative;
    display: block;

    padding: 1em;

    font-family: inherit;
    font-size: 18px;

    line-height: 100%;
    color: $default-color;
    text-decoration: none;

    // white-space: nowrap;

    &:focus,
    &:hover {
      opacity: 0.6;
    }

    &:active::before {
      background-color: $default-color;
    }
  }

}
