.page-header {
  z-index: 9;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;

  padding: 1.5rem 0;
  background-color: $color-brand-opaque;

  transition: $transition-base;

  @media (min-width: $mobile) {
    padding: 2rem 0;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
  }

  &__nav {
    display: none;

    .menu-open & {
      position: relative;
      top: 94px;
      display: block;
      overflow: scroll;
      height: calc(100% - 94px);
      padding-bottom: 45px;
    }
  }

  &__btn-menu {

    outline: none;
    @include BtnReset();
    display: none;

    @media (max-width: $mobile) {
      display: block;
      order: 3;
      position: absolute;
      top: 24px;
      right: 20px;

    }


    &.active {
      & svg {
        width: 29px;
        height: 29px;

        & rect {
          fill: $text-color;
          width: 100%;

          &:nth-child(2) {
            opacity: 0;
          }

          &:first-child {
            transform: rotate(45deg) translate(5px, -8px);
          }
          &:last-child {
            transform: rotate(-45deg) translate(-10px, -2px);
          }
        }
      }
    }

    & svg {
      & rect {
        fill: $default-color;

        transition: 0.3s;

        .scroll-up & {
          fill: $text-color;
        }
      }
    }
  }
}

.menu-open {
  overflow: hidden;
}

.menu-open .navigation {
  display: block;
  height: 100vh;
  background-color: $accent-color;
}

.menu-open .page-header__btn-menu {
  z-index: 10;
}
.menu-open .page-header__logo,
.menu-open .page-header__phone {
  z-index: 9;
}
.menu-open .navigation {
  z-index: 8;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

.scroll-down .page-header,
.scroll-down .page-header__btn-menu {
  transform: translate3d(0, -100%, 0);
}

.scroll-up .page-header {
  padding: 2rem 0;
  transform: none;
}

.scroll-up .page-header__btn-menu {
  top: 2rem;
  right: 2rem;
}

.scroll-up:not(.menu-open) .page-header {
  background-color: #ffffff;
}
