.theme-toggle {
  margin-right: 4rem;

  @media (min-width: $mobile) {
    margin-right: 0;
  }

  & .checkbox {
    opacity: 0;
    position: absolute;
  }

  & .label {
    width: 50px;
    height: 26px;
    // background-color: var(--dark-bg-color);
    background-color: $primary-color;
    display: flex;
    border-radius: 50px;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    position: relative;
    transform: scale(1.5);
  }

  & .ball {
    width: 20px;
    height: 20px;
    background-color: var(--bg-color);
    position: absolute;
    top: 2px;
    left: 2px;
    border-radius: 50%;
    transition: transform 0.2s linear;
  }

  /*  target the elemenent after the label*/
  & .checkbox:checked + .label .ball {
    transform: translateX(24px);
  }

  & .fa-moon {
    color: pink;
  }

  & .fa-sun {
    color: yellow;
  }
}
