.page-footer {
  padding: 1.5rem 0;
  margin-top: auto;
  background-color: $color-brand-opaque;

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media (min-width: $md) {
      display: none;
    }
  }

  &__email {
    @include Link();
    color: var(--text-color);
  }
}
