.social {
  @include ListReset();

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  padding-top: 5px;
  padding-left: 5px;

  &__item {
    width: 48px;
    height: 48px;
    margin: 0 6px;
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;

    text-decoration: none;

    border: 3px solid rgba(40, 54, 69, 0.1);
    border-radius: 50%;
  }
}
