

.page {
  padding: 0;
  margin: 0;
  height: 100%;
  font-family: $mainFont;
  font-weight: $mainFontWeight;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: normal;

  color: var(--text-color);
  background-color: var(--bg-color);
  transition: $transition-base;

  @media (prefers-color-scheme: dark) {
    /* Тёмная тема по умолчанию */
    color: var(--text-color);
    background-color: var(--bg-color);

    &.light-theme {
      color: var(--text-color);
      background-color: var(--bg-color);
    }
  }
}
