// Глобальные переменные
:root {
  --text-color: #222222;
  --bg-color: #ffffff;
  --dark-text-color: #eeeeee;
  --dark-bg-color: #622671;
  --background-dark: #2d3548;
  --text-light: rgba(255, 255, 255, 0.6);
  --text-lighter: rgba(255, 255, 255, 0.9);
  --box-shadow: #cccccc;
  --width-container: 1200px;
}

.dark-theme {
  --text-color: #eeeeee;
  --bg-color: #121212;

  --dark-text-color: #222222;
  --dark-bg-color: #ffffff;

  --box-shadow: #bd93c8;
}

// Font
$mainFont: "Open Sans", "Arial", sans-serif;
$mainFontBold: "Open Sans Bold", "Arial", sans-serif;
$mainFontLight: "Open Sans Light", "Arial", sans-serif;

// Size
$mainFontSize: 16px;
$mainLineHeight: 18px;
$mainFontWeight: 400;
$mainFontWeightBold: 700;
$mainFontWeightLight: 300;

// Main color
$default-color: #ffffff;
$primary-color: #bd93c8;
$accent-color: #622671;
$text-color: #222222;
$bg-color: $primary-color;
$bg-gallery-item: $accent-color;

$color-brand-opaque: rgba($accent-color, 0.6);
$color-brand-menu: rgba($accent-color, 1);
$color-brand-item: rgba($accent-color, 0.85);

$transition-base: 0.5s;
$opacity-base: 0.3;

//
$tablet: 767px;
$mobile-xs: 320px;
$mobile: 769px;
$desktop: 1023px;
$maxWidth: 1400px;

$sm: 576px;
$md: 768px;
