.gallery {
  margin-top: 75px;
  padding: 1.5em 0;

  &__wrapper {
    margin: 1.5em 0;
    padding: 0;
    column-gap: 1.5em;

    @media (min-width: $sm) {
      column-count: 2;
    }

    @media (min-width: $md) {
      column-count: 3;
    }
  }

  &__item {
    position: relative;
    display: inline-block;
    width: 100%;
    background-color: var(--dark-bg-color);

    padding: 0.5em;
    margin: 0 0 0.5em;

    box-shadow: 2px 2px 4px 0 var(--box-shadow);

    overflow: hidden;


    transition: $transition-base;

    &:hover {


      & .gallery__info {
        opacity: 1;
      }

      & img {
        transform: scale(1.05) translateZ(0);
      }
    }

    & .gallery__info {
      opacity: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      padding: 0.5em;
      background-color: $color-brand-item;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      text-align: center;
      color: $default-color;
    }

    & img {
      max-width: 100%;
      height: auto;
      display: block;
      transform-origin: center;
      transform: scale(1) translateZ(0);
    }
  }
}
